<template>
<div>
  <h2>这是Count组件</h2>

  <p>

    Count的值为{{ count }}
  </p>
  <button @click="add">添加</button>
  <button @click="count++">上面button的简写</button>
</div>
</template>

<script>
export default {
  name: "Count",
  data() {
    return{
      count:this.init
    }
  },
  methods:{
    add(){
      this.count ++
      this.$emit('sendToF',this.count)
    }
  },
  //props是自定义属性，允许使用者通过自定义属性为当前属性指定默认值
  // props:['init']
  //props 自定义属性设置默认值
  props:{
    init:{
      default:99,
      //type为数据类型
      type:Number,
      //require必填项校验
      required:true

    }

  }
}
</script>

