<template>
  <div class="box">


    <h2>222 ---- {{ username }}</h2>
    <button @click="changeUsername" >修改----后面的东西{{ rgb }}</button>
    <right :rightB="{test:1}"></right>
    <left></left>


  </div>
</template>
<script>
//导入ajax请求工具包
import axios from 'axios'
import left from "@/components/Left.vue";
import right from "@/components/Right.vue";
export default {
  //.vue组件中的data不能指向对象，必须指向一个函数，函数中return一个对象
  data(){
    return {
      username:"张三",
      dataCh:{
        age:1
      },
      r:222,
      g:111,
      b:213
    }
  },
  methods:{
    changeUsername(){
      this.dataCh.age = "李四"
    },
    //ajax 请求
    async initListFromAjax(){
      const { data:res } = await axios.get("http://localhost:8888/index?id=1")
      console.log(res)
    }
  },
  //侦听器 作用 监听username 属性变化 出发handler函数 immediate控制是否在浏览器刚刚进入时自动触发一次

  // watch:{
  //   username:{
  //     handler(newVal){
  //       console.log(newVal)
  //     },
  //     immediate:true
  //   }
  // },
  //简写
  // watch:{
  //   username(newVal){
  //     console.log(newVal)
  //   }
  // }
  //侦听器深度监听
  watch:{
    'dataCh.age'(newVal){
      console.log(newVal)
    }
  },
  //计算属性 ${} 为插值表达式 在``中使用
  computed:{
    rgb(){
      return `rgb(${this.r},${this.g},${this.b})`
    }
  },
  //组件注册  通过components注册的组件为私有组件
  components:{
    left,
    right

  },
  beforeCreate() {
    console.log("这是生命周期函数刚开始创建组件时触发")
    //props data methods 未初始化不能使用
  },
  created() {
    //初始化完成props data methods
    //一般在这个时间进行ajax请求数据
    this.initListFromAjax()
  },
  beforeMount() {
  },
  mounted() {
  },
  beforeUpdate() {
  },
  updated() {
  },
  beforeDestroy() {
  },
  destroyed() {
  },

}
</script>
<style lang="less" scoped>
.box{
  background-color: red;
  button{
    background-color: green;
  }
}
</style>